const db = wx.cloud.database()
export default {
	data() {
		return {
			list: []
		};
	},
	methods: {
		toVideo(url) {
			this.$store.commit('changeVideoUrl', url)
			uni.navigateTo({
				url: '../video/video',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		
		async getList() {
			try{
				let res = await db.collection('videos').get()
				this.list = res.data
			}catch(e){
				console.log(e)
			}
		}
	},
	onLoad() {
		this.getList()
	}
}
